.everything{position:absolute;display:flex;flex-direction:column;align-items:center}.everything .main{display:flex}.everything .main .leftSide,.everything .main .rightSide{border-radius:2rem;overflow:hidden;display:flex;flex-direction:column;align-items:center;padding:4rem 3rem 2.5rem;background-color:#fff}.everything .main .leftSide{margin-right:2.5rem;width:80rem}.everything .main .leftSide .titleContainer{width:100%;display:flex;justify-content:space-between}.everything .main .leftSide .titleContainer .name,.everything .main .leftSide .titleContainer .dayCount{color:#24292f;font-size:3rem;line-height:3.5rem;font-weight:200}.everything .main .leftSide .titleContainer .name{font-style:italic}.everything .main .leftSide .mapContainer{margin-top:4rem;margin-left:-4rem;width:80rem;height:45rem;display:flex;justify-content:flex-end;align-items:center}.everything .main .leftSide .mapContainer.largeMapContainer{width:80rem}.everything .main .leftSide .tip{height:1rem;margin-bottom:2rem;color:#757575;font-weight:300;font-size:1.6rem}.everything .main .leftSide .dateChanger{display:flex;width:100%}.everything .main .leftSide .dateChanger .backBtn,.everything .main .leftSide .dateChanger .forwardBtn{cursor:pointer;width:11rem;height:4rem;border-radius:2rem;background-color:#319fff;display:flex;justify-content:center;align-items:center;padding-top:.2rem;transition:.3s;color:#fff;font-size:2rem;font-weight:500;text-transform:uppercase}.everything .main .leftSide .dateChanger .backBtn:hover,.everything .main .leftSide .dateChanger .forwardBtn:hover{background-color:#1391ff}.everything .main .leftSide .dateChanger .backBtn{margin-right:2.5rem}.everything .main .leftSide .dateChanger .forwardBtn{margin-left:2.5rem}.everything .main .leftSide .dateChanger .currentDateContainer{display:flex;justify-content:center;flex:auto;height:4rem;border-radius:2rem;border:.2rem solid #bee1ff;background-color:#eaf5ff}.everything .main .leftSide .dateChanger .currentDateContainer p{color:#319ff